.wrapper {
  position: relative;
  background: var(
    --color-background-dark,
    #000b1e
  ); // Use CSS variable for better theming
  overflow: hidden;
  width: 100%;

  // Position the animation
  app-network-animation {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  // Content wrapper
  .flex {
    position: relative;
    z-index: 1;
  }

  .logo-filter {
    filter: brightness(0) invert(1);
    transition: filter 0.2s ease; // Smooth transition for any hover states
  }
}
